<template>
	<Cell :render="this.params.colDef.render"
		  :node="this.params.node"
		  :row='this.params.data'
		  :index="this.params.rowIndex"
		  :column="this.params.colDef">
	</Cell>
</template>
<script>
import Cell from './expand'

export default {
	name: 'TableCell',
	components: { Cell }
}
</script>



