<template>
	<Header :render="this.params.column.colDef.renderHeader"
			:index="this.params.column.colDef.id"
			:column="this.params.column.colDef">
	</Header>
</template>
<script>
import Header from './header'

export default {
	name: 'TableHead',
	components: { Header }
}
</script>




