<template>
	<div ref='myNumber'
		 :class="{'ag-table' : true, 'ag-border': this.border,'ag-itemHeigth':this.itemHeight}">
		<ag-grid-vue ref="agTabel"
					 :style="styles"
					 :class="classess"
					 :rowData="rebuildData"
					 skipHeaderOnAutoSize
					 :suppressAutoSize='true'
					 :headerHeight='headerHeight'
					 :suppressScrollOnNewData='true'
					 :columnDefs="makeColumns"
					 :animateRows="animateRows"
					 :rowHeight='itemHeight'
					 :suppressRowClickSelection='suppressRowClickSelection'
					 :suppressColumnVirtualisation='suppressColumnVirtualisation'
					 :rowMultiSelectWithClick="rowSelect"
					 :getRowClass="getRowClass"
					 :suppressRowHoverHighlight="disabledHover"
					 :suppressCellSelection='true'
					 multiSortKey="ctrl"
					 :rowSelection='rowSelection'
					 :overlayNoRowsTemplate="overlayNoRowsTemplate"
					 :loadingOverlayComponentParams="loadingOverlayComponentParams"
					 :loadingOverlayComponentFramework='loadingOverlayComponentFramework'
					 :defaultColDef="defaultColDef"
					 :pinnedBottomRowData="summationData"
					 :getRowStyle="getRowStyle"
					 :gridOptions='gridOptions'
					 :tooltipShowDelay="tooltipShowDelay"
					 :suppressDragLeaveHidesColumns="true"
					 :isRowSelectable="isRowSelectable"
					 @grid-ready="onReady"
					 @rowDataChanged='onRowDataChanged'
					 @row-clicked="onRowClicked"
					 @cell-double-clicked="onCellDoubleClicked"
					 @row-selected="onRowSelected"
					 @bodyScroll="onBodyScroll"
					 @selectionChanged='onSelectionChanged'
					 @column-resized='onColumnResized'
					 @cell-key-down='onCellKeyDown'
					 @drag-stopped='onDragStopped'
					 @column-moved='onColumnMoved'>
		</ag-grid-vue>
		<!-- @sortChanged='onSortChanged' -->

	</div>
</template>
<script>
//引入ag-grid-vue
import { AgGridVue } from 'ag-grid-vue'
import CustomLoadingOverlay from './components/customLoading'
import CustomPinnedRowRenderer from './components/customPinnedRowRendererVue'
import { deepCopy } from './until.js'
import TableCell from './components/tableCell'
import TableHeader from './components/tableHeader'

// ag-Grid 全局样式
import 'ag-grid-community/dist/styles/ag-grid.css'
import 'ag-grid-community/dist/styles/ag-theme-balham.css'

export default {
	components: {
		AgGridVue,
		TableCell,
		TableHeader,
		CustomLoadingOverlay,
		CustomPinnedRowRenderer
	},
	data() {
		return {
			rebuildData: [],
			gridOptions: null,
			gridApi: null,
			columnApi: null,
			lastIndexRow: null,
			loadingOverlayComponentFramework: null,
			loadingOverlayComponentParams: null,
			defaultColDef: null,
			cloneColumns: deepCopy(this.columns),
			getRowStyle: null,
			isLastColumn: false,
			isRowSelectable: null,
			eventObj: {
				eventType: ''
			},
			defaultTrigger: true,
			getRowClass: null,
			rowKey: 1,
			isEventTag: false,
			isClickAllSelTag: false
		}
	},
	props: {
		// 滚动条宽度设置
		scrollbarwidth: {
			type: Number,
			default: 15
		},
		border: {
			type: Boolean,
			default: false
		},
		// 数据源
		data: Array,
		// 列
		columns: Array,
		// 行高
		itemHeight: {
			type: Number,
			default: 28
		},
		// 单选或多选时支持点击行进行选中,再次点击不进行反选，多选时仅能通过勾选框反选(不能与rowSelect同时使用,需要开启highlight-row)
		rowSelectOnly: {
			type: Boolean,
			default: false
		},
		highlightRow: {
			type: Boolean,
			default: false
		},
		// 暂无数据
		noDataText: {
			type: String,
			default: '暂无数据'
		},
		width: {
			type: [Number, String]
		},
		height: {
			type: [Number, String],
			default: 400
		},
		// 汇总
		summationData: {
			type: Array,
			default: null
		},
		//禁用鼠标悬停时的高亮
		disabledHover: {
			type: Boolean,
			default: false
		},
		// 行类
		rowClassName: {
			type: Function,
			default() {
				return ''
			}
		},
		//多选时是否支持点击行选中
		rowSelect: {
			type: Boolean,
			default: false
		},
		// 显示表头
		showHeader: {
			type: Boolean,
			default: true
		},
		loading: {
			type: Boolean,
			default: false
		},
		// 多级表头
		multiLevel: {
			type: Array,
			default: null
		},
		// loading文案
		loadingText: {
			type: String,
			default: undefined
		},
		// 滚动到距离顶部固定的位置
		scrollTopSet: {
			type: Number,
			default: 0
		},
		//是否能列宽拖动
		canDrag: {
			type: Boolean,
			default: true
		},
		//是否能列移动
		canMove: {
			type: Boolean,
			default: false
		},
		showTitle: {
			type: Boolean,
			default: false
		},
		//支持渲染不可见的列，会影响性能
		suppressColumnVirtualisation: {
			type: Boolean,
			default: false
		},
		//是否有最后一列空白，o45那边需要
		hasLastColumn: {
			type: Boolean,
			default: false
		},
		// tooltip显示时间
		tooltipShowDelay: {
			type: [Number, String],
			default: 0
		},
		// 如果为true，则单击行时将不会发生行选择。仅在需要复选框选择时使用
		suppressRowClickSelection: {
			type: Boolean,
			default: false
		},
		// 斑马纹
		stripe: {
			type: Boolean,
			default: false
		},
		// 排序选中置顶
		supportCheckSortTop: {
			type: Boolean,
			default: false
		},
		//行拖动，排序动画
		animateRows: {
			type: Boolean,
			default: false
		}
	},
	computed: {
		// 是否多选
		rowSelection() {
			if (this.highlightRow) return 'multiple'
			if (this.rowSelectOnly) return 'multiple'
			if (this.rowSelect) return 'multiple'
		},
		classess() {
			return [
				'flex-grow-1',
				'flex-shrink-1',
				'ag-theme-balham',
				'ag-fresh',
				{
					['ag-stripe-has']: this.stripe,
					['ag-stripe-not']: !this.stripe
				}
			]
		},
		//列属性替换
		tbColumns() {
			let itemArr = []

			this.columns.map(col => {
				// type = index
				if (col.type === 'index') {
					itemArr.push({
						headerName: col.title || '#',
						field: 'indexCol' || null,
						width: 60,
						sortable: false,
						cellRenderer: function(params) {
							return parseInt(params.node.rowIndex) + 1
						},
						suppressSizeToFit: true,
						pinned: 'left',
						lockPinned: true,
						cellClass: 'ag-lock-pinned-index',
						headerClass: 'ag-header-index',
						lockPosition: true,
						resizable: false
					})
				} // type = selection
				else if (col.type === 'selection') {
					itemArr.push({
						headerName: col.title,
						field: col.key,
						resizable: false,
						// 表头是否有复选框，全选用的
						headerCheckboxSelection: col.selectColumn,
						suppressSizeToFit: true,
						// 单选
						checkboxSelection: true,
						width: col.width || 30,
						minWidth: col.minWidth,
						maxWidth: col.maxWidth,
						pinned: 'left',
						lockPinned: true,
						cellClass: 'ag-lock-pinned-selection',
						headerClass: 'ag-header-selection',
						lockPosition: true,
						valueFormatter: col.formatMethod,
						tooltipField: this.showTitle
							? col.formatMethod || col.render
								? 'undefined'
								: col.key
							: undefined,
						// 列提示自定义文本
						tooltipValueGetter: this.showTitle
							? col.titleFormatMethod
							: undefined // render
					})
				} else if (col.type === 'drag') {
					itemArr.push({
						headerName: '#',
						field: null,
						width: 40,
						sortable: false,
						pinned: 'left',
						suppressSizeToFit: true,
						lockPinned: true,
						lockPosition: true,
						resizable: false,
						rowDrag: true,
						rowDragText: () => {
							return null
						}
					})
				} else {
					itemArr.push({
						headerName: col.title,
						headerClass: col.headerClass,
						field: col.key,
						width: col.width,
						minWidth: col.minWidth,
						maxWidth: col.maxWidth,
						// 固定
						pinned: col.fixed,
						// 行样式
						cellStyle:
							col.align == 'right'
								? { 'text-align': 'right' }
								: col.align == 'center'
								? { 'text-align': 'center' }
								: { 'text-align': 'left' },
						// 文本表达式
						valueFormatter: col.formatMethod,
						// 列提示
						headerTooltip: this.showTitle
							? col.headerTooltip || col.title
							: undefined,

						tooltipField: this.showTitle
							? col.formatMethod || col.render
								? undefined
								: col.key
							: undefined,

						// 列提示自定义文本
						tooltipValueGetter: this.showTitle
							? col.titleFormatMethod
							: undefined, // render
						cellRendererFramework: col.render ? 'TableCell' : '',
						headerComponentFramework: col.renderHeader
							? 'TableHeader'
							: '',
						// 可以编辑
						editable: col.editable,
						unSortIcon: col.unSortIcon,
						// 单选
						checkboxSelection: col.checkboxSelection,
						// render
						render: col.render,
						renderHeader: col.renderHeader,
						// 筛选
						sortable: col.sortable,
						formatPinned: col.formatPinned,
						// 自定义排序方法
						comparator: col.sortMethod,
						// asc dese  排序方式
						sort: col.sortType,
						// 是否隐藏列
						hide: col.hiddenCol || false,
						// 能否拖动位置（冻结列）
						lockPosition: col.fixed,
						// 是否可以拖拽列的宽度
						resizable: col.fixed
							? false
							: this.canDrag
							? true
							: false,
						lockPinned: true,
						// 用户自定义数据
						layout_id: col.layout_id || '',
						display_order: col.display_order || null,
						field_caption: col.field_caption || null,
						field_name: col.field_name || null,
						visible_flag: col.visible_flag || null,
						print_flag: col.print_flag || null,
						field_fix_show_flag: col.field_fix_show_flag || null,
						cellClass: col.className,
						flex: col.flex
					})
				}
			})
			//o45目前是用了这块逻辑
			if (this.isLastColumn && this.hasLastColumn) {
				itemArr.push({ flex: 1 })
			}
			return itemArr
		},
		makeColumns() {
			let multiLevelArray = []
			if (this.multiLevel) {
				const columnDefs = this.tbColumns
				this.multiLevel.forEach(item => {
					var jsonItem = {}
					jsonItem.headerName = item.title
					jsonItem.children = columnDefs.splice(0, item.cols)
					jsonItem.marryChildren = true
					jsonItem.headerClass =
						item.align === 'center' ? 'ag-header-text-center' : null
					multiLevelArray.push(jsonItem)
				})
			}

			return this.multiLevel ? multiLevelArray : this.tbColumns
		},
		styles() {
			let style = {}
			if (this.width) style.width = `${this.width}px`
			style.height = this.height ? `${this.height}px` : '1000px'
			return style
		},
		overlayNoRowsTemplate() {
			return `<span style="padding: 10px; ">${this.noDataText}</span>`
		},
		headerHeight() {
			return this.showHeader ? 30 : 0
		}
	},
	watch: {
		data: {
			handler(val) {
				this.rebuildData = this.makeData()
			},
			deep: true
		},
		scrollTopSet() {
			this.gridApi.ensureIndexVisible(
				this.scrollTopSet / this.itemHeight,
				'top'
			)
		},
		loading: {
			handler(val) {
				val
					? this.gridApi.showLoadingOverlay()
					: this.data.length > 0
					? this.gridApi && this.gridApi.hideOverlay()
					: this.gridApi.showNoRowsOverlay()
			}
		}
	},

	beforeMount() {
		this.gridOptions = {
			// todo 行拖动  动画
			rowDragManaged: true,
			// 排序后的数据处理
			postSort: rowNodes => {
				function isChecked(node) {
					return node.data.check
				}
				function move(toIndex, fromIndex) {
					rowNodes.splice(
						toIndex,
						0,
						rowNodes.splice(fromIndex, 1)[0]
					)
				}
				var nextInsertPos = 0

				if (this.supportCheckSortTop) {
					for (var i = 0; i < rowNodes.length; i++) {
						if (isChecked(rowNodes[i])) {
							move(nextInsertPos, i)
							nextInsertPos++
						}
					}
				}
			}
		}
		// loading组件的参数
		this.loadingOverlayComponentParams = {
			loadingMessage: this.loadingText || '加载中...'
		}
		this.loadingOverlayComponentFramework = 'CustomLoadingOverlay'

		this.defaultColDef = {
			flex: 1,
			suppressMovable: !this.canMove,
			pinnedRowCellRendererFramework: 'CustomPinnedRowRenderer'
		}

		// 隐藏某行的复选框
		this.isRowSelectable = rowNode => {
			return rowNode.data ? !rowNode.data._disabled : false
		}

		this.getRowClass = params => {
			return this.rowClassName(params.data, params.rowIndex)
		}

		// 汇总行颜色
		this.getRowStyle = params => {
			if (params.node.rowPinned) {
				return { 'background-color': '#EAF5FF' }
			}
		}
	},

	methods: {
		// 键盘监控
		onCellKeyDown(e) {
			const node = e.node

			const keyCode = e.event.keyCode
			// next
			if (keyCode === 40) {
				this.gridApi.deselectAll()
				node.setSelected(true)
			}
			// prev
			if (keyCode === 38) {
				this.gridApi.deselectAll()
				node.setSelected(true)
			}
			// left
			if (keyCode === 37) {
			}
			// right
			if (keyCode === 39) {
			}
		},

		// 表格加载完成执行
		onReady(params) {
			let _this = this
			this.gridApi = params.api
			this.columnApi = params.columnApi

			this.loading
				? this.gridApi.showLoadingOverlay()
				: this.data.length > 0
				? this.gridApi.hideOverlay()
				: this.gridApi.showNoRowsOverlay()

			//427-443行主要是处理空白列的问题
			var node = document.getElementsByClassName('flex-grow-1')[0]
				.offsetWidth
			var node1 =
				document.getElementsByClassName('ag-center-cols-container')[0]
					.offsetWidth + 58

			if (node1 > node) {
				this.isLastColumn = false
				var allColumnIds = []
				params.columnApi.getAllColumns().forEach(function(column) {
					if (column.colId !== '0') {
						allColumnIds.push(column.colId)
					}
				})
				// params.api.sizeColumnsToFit()
				//
				// o45 499  其他 500行
				params.columnApi.autoSizeColumns()
				// params.columnApi.autoSizeColumns(allColumnIds, false)
			} else {
				this.isLastColumn = true
				params.columnApi.autoSizeColumns()
			}

			// 边框
			if (this.border) {
				this.customClass = `${this.customClass} .ag-theme-balham .ag-ltr .ag-cell`
			}

			// 滚动到固定的位置
			this.gridApi.ensureIndexVisible(
				this.scrollTopSet / this.itemHeight,
				'top'
			)

			//  监听全选按钮点击事件
			document
				.getElementsByClassName('ag-header-selection')[0]
				.querySelectorAll('div')[0]
				.querySelector('div').onclick = function() {
				_this.isClickAllSelTag = true

				// 获取所有选中的数据
				const selectedRows = _this.gridApi.getSelectedRows()
				// 获取选中的所有索引
				let selectIndex = []
				;(_this.gridApi.getSelectedNodes() || []).map(item => {
					selectIndex.push(item.rowIndex)
				})

				_this.$emit('on-select-all-click', selectedRows, selectIndex)
				setTimeout(() => {
					_this.isClickAllSelTag = false
				}, 500)
			}
		},

		// 监听数据变化的api
		onRowDataChanged(params) {
			// 默认选中_checked属性行
			// if (this.data.length > 0) {
			// 	params.api.forEachNode(function(node, index) {
			// 		if (node.data._checked) {
			// 			node.setSelected(true)
			// 			node.data._checkIndex = 1
			// 		}
			// 	})
			// }
		},

		// 获取选中的数据
		getSelection() {
			return this.gridApi.getSelectedRows()
		},

		// 点击行事件
		onRowClicked(event) {
			// 记录最后一次的点击复选框的索引
			this.lastIndexRow = event.rowIndex
			this.$emit('on-row-click', event.data, event.rowIndex)
		},
		// 双击单元格事件
		onCellDoubleClicked(event) {
			let obj = {
				index: event.rowIndex,
				status: event.node.selected
			}
			this.$emit('on-row-dblclick', event.data, obj)
		},

		//单击复选框
		onRowSelected(event) {
			if (event.data._disabled) {
				event.node.setSelected(false)
			}
			this.lastIndexRow = event.rowIndex

			if (this.supportCheckSortTop) {
				event.data.check = event.node.selected
			}

			// 获取所有选中的数据
			let selectedRows = this.gridApi.getSelectedRows()
			if (!this.isEventTag) {
				if (!this.isClickAllSelTag) {
					this.$emit(
						'on-select',
						selectedRows,
						event.data,
						event.node,
						event.rowIndex
					)
				}
			}
		},
		// body滚动
		onBodyScroll(params) {
			let pos = params.direction === 'vertical' ? 'y' : 'x'
			let num =
				params.direction === 'vertical'
					? this.gridApi.getDisplayedRowCount() *
							this.gridApi.getSizesForCurrentTheme().rowHeight -
					  this.gridApi.getVerticalPixelRange().bottom
					: params.left
			this.$emit('on-scroll', num, pos)
		},
		// 拖动表格
		onColumnMoved(cell) {
			if (this.eventObj) {
				this.eventObj = {}
			}
			this.updateColumns()

			const allColumns = cell.columnApi.getAllColumns()
			if (cell.column) {
				allColumns.map((col, index) => {
					if (col.colId === cell.column.colId) {
						this.$emit('on-move', index, cell.toIndex)
						this.eventObj = {
							eventType: 'onMove',
							colId: index,
							toIndex: cell.toIndex
						}
					}
				})
			}
		},
		// 列宽改变
		onColumnResized(cell) {
			if (this.eventObj) {
				this.eventObj = {}
			}
			this.updateColumns()
			this.$emit(
				'on-drag',
				cell.column ? cell.column.actualWidth : '',
				cell.column ? cell.column.colId : ''
			)
			this.eventObj = {
				eventType: 'onDrag',
				actualWidth: cell.column ? cell.column.actualWidth : '',
				colId: cell.column ? cell.column.colId : ''
			}
		},

		onDragStopped(cell) {
			this.updateColumns()
			// 拖动多级表头不触发事件
			if (this.eventObj.eventType === '' || this.eventObj.colId === '')
				return
			this.$emit('on-drag-stopped', this.eventObj)
		},
		// 更新列
		updateColumns() {
			let newArray = []
			this.columnApi.getAllGridColumns().map(item => {
				newArray.push({
					title: item.colDef.headerName,
					key: item.colDef.field,
					width: item.colDef.width,
					minWidth: item.colDef.minWidth,
					maxWidth: item.colDef.maxWidth,
					fixed: item.colDef.pinned,
					formatMethod: item.colDef.valueFormatter,
					editable: item.colDef.editable,
					checkboxSelection: item.colDef.checkboxSelection,
					sortable: item.colDef.sortable,
					sortMethod: item.colDef.comparator,
					sortType: item.colDef.sort,
					layout_id: item.colDef.layout_id,
					display_order: item.colDef.display_order,
					field_name: item.colDef.field_name,
					visible_flag: item.colDef.visible_flag,
					print_flag: item.colDef.print_flag,
					field_fix_show_flag: item.colDef.field_fix_show_flag
				})
			})
			this.cloneColumns = newArray
		},
		// 选择发生改变
		onSelectionChanged(event) {
			// 获取所有选中的数据
			let selectedRows = this.gridApi.getSelectedRows()
			// 获取选中的所有索引
			let selectIndex = []
			;(this.gridApi.getSelectedNodes() || []).map(item => {
				selectIndex.push(item.rowIndex)
			})

			// 由于onRowSelected事件选中和取消选中会触发两次，导致返回的当前索引不正确，so用了两个变量来记录
			if (!this.isEventTag) {
				this.$emit(
					'on-selection-change',
					selectedRows,
					selectIndex,
					this.lastIndexRow
				)
			}

			this.lastIndexRow = null
		},

		// todo 暂时加上这个获取焦点的方法
		toggleIsCurrent(params) {},
		// 设置列以调整大小以适合水平网格
		sizeColumnsToFit() {
			this.gridApi.sizeColumnsToFit()
		},
		//
		autoSizeAllColumns(skipHeader = false) {
			var allColumnIds = []
			this.columnApi.getAllColumns().forEach(function(column) {
				allColumnIds.push(column.colId)
			})
			this.columnApi.autoSizeColumns(allColumnIds, skipHeader)
		},
		/** 新增数据
		 * @method addData
		 * @param addIndex: 插入位置
		 * @param newRows: 插入数据
		 */
		addData(newRows, addIndex) {
			this.gridApi.applyTransaction({ add: newRows, addIndex: addIndex })
		},
		/** 删除数据
		 * @method removeData
		 * @param selRows: 选中的数据
		 */
		removeData(newData) {
			this.gridApi.applyTransaction({ remove: newData })
		},
		/** 删除选中的数据
		 * @method removeSelectData
		 * @param selRows: 选中的数据
		 */
		removeSelectData() {
			var selectedData = this.gridApi.getSelectedRows()
			this.gridApi.applyTransaction({ remove: selectedData })
		},
		/** 更新数据
		 * @method updateData
		 * @param newData: 更新数据
		 */
		updateData(newData) {
			this.gridApi.applyTransaction({ update: newData })
		},
		// 获取当前页面数据
		getCurruntData() {
			let data = []
			this.gridApi.forEachNode(function(node) {
				data.push(node.data)
			})
			return data
		},
		// 赋值column
		setColumnDefs(newColumn) {
			let itemArr = []
			newColumn.forEach(col => {
				// type = index
				if (col.type === 'index') {
					itemArr.push({
						headerName: '#',
						field: null,
						width: 32,
						cellRenderer: function(params) {
							return parseInt(params.node.id) + 1
						},
						suppressSizeToFit: true,
						pinned: 'left',
						lockPinned: true,
						cellClass: 'ag-lock-pinned',
						headerClass: 'ag-header-selection',
						lockPosition: true
					})
				} // type = selection
				else if (col.type === 'selection') {
					itemArr.push({
						suppressSizeToFit: true,
						headerName: null,
						// 表头是否有复选框，全选用的
						headerCheckboxSelection: col.selectColumn,
						field: null,
						// 单选
						checkboxSelection: true,
						width: 30,
						pinned: 'left',
						lockPinned: true,
						cellClass: 'ag-lock-pinned',
						headerClass: 'ag-header-selection',
						lockPosition: true
					})
				} else {
					itemArr.push({
						headerName: col.title,
						headerClass: col.headerClass,
						field: col.key,
						width: col.width,
						minWidth: col.minWidth,
						maxWidth: col.maxWidth,
						// 固定
						pinned: col.fixed,
						// 行样式
						cellStyle:
							col.align == 'right'
								? { 'text-align': 'right' }
								: col.align == 'center'
								? { 'text-align': 'center' }
								: { 'text-align': 'left' },
						// 文本表达式
						valueFormatter: col.formatMethod,
						// 列提示
						headerTooltip: col.headerTooltip || col.title,
						tooltipField: this.showTitle
							? col.formatMethod || col.render
								? undefined
								: col.key
							: undefined,
						// 列提示自定义文本
						tooltipValueGetter: this.showTitle
							? col.titleFormatMethod
							: undefined, // render

						cellRendererFramework: col.render ? 'TableCell' : '',
						headerComponentFramework: col.renderHeader
							? 'TableHeader'
							: '',
						// 可以编辑
						editable: col.editable,
						// 单选
						checkboxSelection: col.checkboxSelection,
						// render
						render: col.render,
						// 筛选
						sortable: col.sortable,
						// 自定义排序方法
						comparator: col.sortMethod,
						// asc dese  排序方式
						sort: col.sortType,
						// 是否隐藏列
						hide: col.hiddenCol || false,
						// 能否拖动位置（冻结列）
						lockPosition: col.fixed,
						// 是否可以拖拽列的宽度
						resizable:
							col.fixed &&
							(col.fixed === 'left' || col.fixed === 'right')
								? false
								: col.resizable
								? true
								: false,
						lockPinned: true,
						// 用户自定义数据
						layout_id: col.layout_id || '',
						display_order: col.display_order || null,
						field_caption: col.field_caption || null,
						field_name: col.field_name || null,
						visible_flag: col.visible_flag || null,
						print_flag: col.print_flag || null,
						field_fix_show_flag: col.field_fix_show_flag || null,
						cellClass: col.className,
						flex: col.flex
					})
				}
			})

			this.gridApi.setColumnDefs(itemArr)
		},
		makeData() {
			let data = deepCopy(this.data)
			// data.forEach((row, index) => {
			// 	row._index = index
			// 	// row._checkIndex = row._checked
			// })
			return data
		},
		// 设置行数据
		setRowData(arr) {
			this.gridApi.setRowData(arr)
		},
		// 批量设置行选中 场外专用api
		setRowSelect(items = []) {
			let _this = this
			_this.isEventTag = true
			items.map(item => {
				_this.gridApi.getRowNode(item).setSelected(true)
			})
			setTimeout(() => {
				_this.isEventTag = false
			}, 500)
		},
		// 批量设置行选中，会触发on-select,on-select-change
		selectRows(items = []) {
			let _this = this
			items.map(item => {
				_this.gridApi.getRowNode(item).setSelected(true)
			})
		},
		// 批量设置行选中，会触发on-select,on-select-change
		deselectRows(items = []) {
			let _this = this
			items.map(item => {
				_this.gridApi.getRowNode(item).setSelected(false)
			})
		},

		// 获得可视区域的第一行index
		getFirstIndex(index) {
			return this.gridApi.getDisplayedRowAtIndex(index)
		},
		// 获得可视区域的最后一行index
		getLastIndex() {
			return this.gridApi.getDisplayedRowCount()
		},
		selectAll() {
			this.gridApi.selectAll()
		},
		deselectAll() {
			this.gridApi.deselectAll()
		},
		selectToUp() {
			// 当前所有的数据
			let curData = this.getCurruntData()
			// 选中的数据
			let selData = this.getSelection()

			// 合并数据去重
			const arr = Array.from(new Set([...selData, ...curData]))
			let selectArr = selData.map((item, index) => index)

			// 赋值数据源
			this.setRowData(arr)
			this.$nextTick(() => {
				// 默认选中的数据
				this.setRowSelect(selectArr)
			})
		},
		// 获取所有的节点
		getAllNodes() {
			let nodes = []
			this.gridApi.forEachNode(node => {
				nodes.push(node)
			})
			return nodes
		}
	}
}
</script>
<style >
.my-css-class .ag-header-cell-label {
	flex-direction: row-reverse;
}
label {
	font-weight: normal !important;
}

.ag-theme-balham .ag-header-cell::after,
.ag-theme-balham .ag-header-group-cell::after {
	height: 100% !important;
	top: 0 !important;
}

/* 单元格padding */
.ag-theme-balham .ag-cell {
	padding: 0 3px !important;
}

.ag-theme-balham .ag-header-cell {
	padding: 0 14px !important;
}
.ag-table .ag-theme-balham .ag-cell.ag-cell-value {
	padding-left: 8px;
	padding-right: 8px;
}
/* 选择框 */
.ag-checkbox.ag-input-field {
	cursor: pointer;
	font-size: 12px;
	display: inline-block;
	margin-left: 2px;
	line-height: 1;
	max-width: 100%;
	font-weight: 700;
}

/* 选项框 */
.ag-wrapper.ag-input-wrapper.ag-checkbox-input-wrapper {
	display: inline-block;
	vertical-align: middle;
	white-space: nowrap;
	cursor: pointer;
	outline: none;
	line-height: 1;
	position: relative;
	width: 16px;
	height: 16px;
	position: relative;
	top: 0;
	left: 0;
	border: 1px solid #d7dde4;
	border-radius: 0px;
	background-color: #fff;
}
/* 头部多选 */
.ag-table .ag-theme-balham .ag-ltr .ag-header-select-all {
	margin-right: 4px;
	margin-left: -8px;
}

/* 多选框选中 */
.ag-table .ag-theme-balham .ag-checkbox-input-wrapper .ag-indeterminate::after,
.ag-table .ag-wrapper.ag-input-wrapper.ag-checkbox-input-wrapper::after,
.ag-table .ag-theme-balham .ag-checkbox-input-wrapper .ag-checked::after {
	content: '';
	display: table;
	width: 7px;
	height: 10px;
	position: absolute;
	top: 0px;
	left: 4px;
	border: 2px solid #fff;
	border-top: 0;
	border-left: 0;
}

.ag-table
	.ag-theme-balham
	.ag-ltr
	.ag-has-focus
	.ag-cell-focus:not(.ag-cell-range-selected) {
	border-color: transparent;
}

/* .ag-theme-balham .ag-cell-focus,
.ag-cell-no-focus {
	border: none !important;
}

.ag-theme-balham .no-border.ag-cell:focus {
	border: none !important;
	outline: none;
} */

/* 冻结列阴影 */
.ag-pinned-left-header {
	box-shadow: 2px 0 6px -2px rgba(0, 0, 0, 0.2);
}
.ag-pinned-left-cols-container {
	box-shadow: 2px 0 6px -2px rgba(0, 0, 0, 0.2);
	z-index: 2;
}

.ag-input-field-input.ag-checkbox-input {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	z-index: 1;
	cursor: pointer;
	opacity: 0;
}

.ag-wrapper.ag-input-wrapper.ag-checkbox-input-wrapper.ag-checked {
	border-color: #298dff;
	background-color: #298dff;
}
.ag-wrapper.ag-input-wrapper.ag-checkbox-input-wrapper.ag-checked::after {
	transform: rotate(45deg) scale(1);
}
.ag-theme-balham .ag-selection-checkbox {
	margin-top: -1px;
}

/* icon */
.ag-table .ag-theme-balham .ag-icon {
	font-family: 'iconfont' !important;
	font-weight: bold;
}
/* 正序 */
.ag-table .ag-theme-balham .ag-icon-asc:before {
	content: '\E8EA';
}
/* 倒序 */
.ag-table .ag-theme-balham .ag-icon-desc:before {
	content: '\E764';
}
/* 默认 */
.ag-table .ag-theme-balham .ag-icon-none::before {
	content: '\e988';
	font-size: 14px;
}

/* 行拖动图标 */
.ag-table .ag-theme-balham .ag-icon-grip::before {
	content: '\E61A';
	font-size: 14px;
}

/* 行选中、行hover设置 */
.ag-table .ag-theme-balham .ag-row-selected {
	background-color: #d5dde6;
}
.ag-table .ag-theme-balham .ag-row-hover {
	background-color: #eaf5ff;
}
.ag-stripe-has .ag-row-odd {
	background-color: #fafafa;
}
.ag-stripe-has .ag-row-even {
	background-color: #ffffff;
}
.ag-stripe-not .ag-row-odd {
	background-color: #ffffff;
}
.ag-stripe-not .ag-row-even {
	background-color: #ffffff;
}

/*  纵向分割线border*/
.ag-border .ag-theme-balham .ag-ltr .ag-cell {
	border-right: 1px solid #d7dde4;
}
.ag-itemHeigth .ag-theme-balham .ag-cell {
	line-height: 28px;
}
/* 表格边框 */
.ag-table .ag-theme-balham .ag-root-wrapper {
	border: 1px solid #d7dde4;
}
/* 表头字体颜色 */
/* .ag-table .ag-theme-balham .ag-header-row.ag-header-row-column {
	color: black;
} */
/* 多级表头字体颜色 */
.ag-table .ag-theme-balham .ag-header-group-text {
	color: black;
}

/* 表头背景颜色 */
.ag-header-viewport {
	background-color: #fff;
}
/* tip提示框 */
.ag-theme-balham .ag-tooltip {
	background-color: #fff;
}
/* 多级表头是否居中 */
.ag-header-text-center .ag-header-group-cell-label {
	display: flex;
	justify-content: center;
}

/* loading遮罩层比冻结列的高 */
.ag-theme-balham .ag-overlay-loading-wrapper {
	z-index: 3;
}
/* 行拖动图标居中展示 */
.ag-theme-balham .ag-cell-wrapper .ag-row-drag {
	margin: 0 11px 0 8px;
}

/* 滚动条宽高设置 */
/* ::-webkit-scrollbar {
	width: 16px;
	height: 16px;
	background-color: transparent;
}

::-webkit-scrollbar-track {
	background-color: transparent;
}

::-webkit-scrollbar-thumb {
	border-radius: 10px;
	background: rgba(123, 129, 144, 1);
}

::-webkit-scrollbar-thumb:hover {
	cursor: pointer;
	background: rgba(123, 129, 144, 1);
} */

/* 滚动条固定显示 */
/* .ag-body-viewport {
	overflow-y: scroll !important;
} */

/* rowClassName 样式覆盖*/

/* .demo-table-info-row .ag-cell {
	background: yellow;
}
.demo-table-error-row .ag-cell {
	background: red;
} */

/* 非冻结列 */
/* .ag-center-cols-container .ag-row {
	
	
} */
/* 冻结列 */
/* .ag-pinned-left-cols-container .ag-row {
	
} */

/* 隐藏复选框的span文本 */
/* .ag-theme-balham .ag-lock-pinned span {
	display: none;
} */
/* 隐藏全选框的span文本 */
/* .ag-theme-balham .ag-header-selection span {
	display: none;
} */
</style>


