export default {
  name: 'TableExpand',
  functional: true,
  props: {
    row: Object,
    node: Object,
    render: Function,
    index: Number,
    column: {
      type: Object,
      default: null
    },
  },
  render: (h, ctx) => {
    const params = {
      row: ctx.props.row,
      index: ctx.props.index,
      node: ctx.props.node
    }
    if (ctx.props.column) params.column = Object.assign({}, ctx.props.column, {
      key: ctx.props.column.field,
      title: ctx.props.column.headerName
    })
    return ctx.props.render(h, params)
  }
}


