<template>
	<span :style="params.style">{{valueFormat}}</span>
</template>
<script>
export default {
	name: 'pinnedRow',
	data() {
		return {}
	},
	computed: {
		valueFormat() {
			return this.params.colDef.formatPinned && this.params.value
				? this.toThousands(this.params.value)
				: this.params.value
		}
	},
	methods: {
		//  金额处理
		toThousands(num) {
			var num = (num || 0).toString(),
				result = ''
			while (num.length > 3) {
				result = ',' + num.slice(-3) + result
				num = num.slice(0, num.length - 3)
			}
			if (num) {
				result = num + result
			}
			return result
		}
	}
}
</script>
<style >
</style>




